@import url(http://fonts.googleapis.com/css?family=Roboto+Slab:500,700|Open+Sans:400italic,500,600,700italic,400,700|Inter:400,600,700);

$open-sans: 'Open Sans', sans-serif;
$roboto-slab: 'Roboto Slab', serif;
$inter: 'Inter', sans-serif;

$body-font: $open-sans;
$header-font: $roboto-slab;
$subtitle-font: $inter;

$body-color: #222;
$link-color: #92521b;

@mixin isDesktop {
  @media screen and (min-width: 1025px) {
    @content;
  }
}

@mixin isMobile {
  @media screen and (max-width: 1024px) {
    @content;
  }
}

@mixin isTablet {
  @media screen and (min-width: 768px) and (max-width: 1024px) {
    @content;
  }
}

@mixin isMobileSmall {
  @media screen and (max-width: 767px) {
    @content;
  }
}

// Mobile-only
@include isMobile {
  .desktop-only {
    display: none !important;
  }
}

// Desktop-only
@include isDesktop {
  .mobile-only {
    display: none !important;
  }
}

@include isTablet {
  .tablet-ok {
    display: initial !important;
  } 
}

html, body {
  width: 100%;
  overflow-x: hidden;
}

body {
  display: flex;
  flex-direction: column;
  font-family: $body-font;
  color: $body-color;
  margin: 0;

  @include isDesktop {
    font-size: 18px;
  }
  @include isMobile {
    font-size: 16px;
  }
  @include isTablet {
    font-size: 18px;
  }
}

.site-padding {
  @include isDesktop {
    padding-left: 64px;
    padding-right: 64px;
  }

  @include isMobile {
    padding-left: 16px;
    padding-right: 16px;
  }

  @include isTablet {
    padding-left: 32px;
    padding-right: 32px;
  }
}

a {
  text-decoration: none;
  font-weight: 600;
  color: $link-color;
}

a:hover {
  text-decoration: underline;
  text-underline-offset: 4px;

  @include isMobile {
    text-decoration: none;
  }
}

.header-text {
  font-family: $roboto-slab;
  letter-spacing: 1.5px;
  font-weight: 700;
}

.subtitle-text {
  // font-family: $subtitle-font;
  font-weight: 700;
}

.page-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@include isDesktop {
  .nav-bar {
    display: flex;
    align-items: center;
    padding: 8px;
    background-color: $bg-nav;
    justify-content: space-between;
    line-height: 0;
  }

  .nav-bar-main {
    display: flex;
    align-items: center;
  }

  .fb-logo {
    padding: 8px;
    margin-right: 8px;
  }

  .fb-logo img {
    height: 30px;
    width: 30px;
  }
  
  .nav-bar a,
  .nav-bar a:hover {
    text-decoration: none;
    font-weight: 500;
  }
  
  .nav-item {
    padding: 4px 2px;
    margin: 0 8px;
    border-bottom: 1px solid $bg0;
  }
  
  .nav-item:hover,
  .nav-item.current {
    border-bottom: 1px solid $body-color;
  }
  
  .nav-logo {
    padding: 0 16px;
  }
  
  .nav-logo img {
    height: 64px;
  }
  
  .nav-link {
    color: $body-color;
  }
}

@include isMobile {

  $larger-bar-height: 85px;
  $larger-logo-height: 64px;
  $smaller-bar-height: 68px;
  $smaller-logo-height: 48px;

  .nav {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;
    margin-right: 0;
    background-color: $bg-nav;
  }

  .nav ul {
    display: block;
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  nav ul.nav-logo {
    display: flex;
    align-items: center;
    
    height: $larger-bar-height;
    @include isMobileSmall() {
      height: $smaller-bar-height;
    }
  }

  nav ul.nav-logo li {
    line-height: 0;
    @include isMobileSmall {
      padding-left: 16px;
      padding-right: 0;
    }
  }

  nav ul:last-child {
    width: 100%;
    flex-basis: 100%;
}

  .nav-logo img {
    height: $larger-logo-height;
    
    @include isMobileSmall {
      height: $smaller-logo-height;
    }
  }
  
  nav ul li {
     /* Puts the elements in a single line */
     display: inline-flex;
     width: 100%;
     text-align: right;
     padding: 16px 16px 16px 32px;
     box-sizing: border-box;
  }

  input[type='checkbox']:not(:checked)+ul {
    display: none;
  }

  nav input[type='checkbox'] {
    display: none;
  }

  nav label {
    text-align: right;
    display: block;
    padding-right: 32px;
    line-height: 0;
    align-self: center;
  }

  nav ul.dropdown-nav {
    padding-bottom: 8px;
    position: absolute;
    z-index: 999;
    background-color: $bg-nav;
    box-shadow: 0px 3px 3px 0 #efeeea;

    top: 85px;
    @include isMobileSmall() {
      top: $smaller-bar-height;
    }
  }

  .material-symbols-outlined.hamburger-icon {
    font-size: 32px;
  }
}

.banner-img {
  width: 100%;
}

.banner-image {
  height: 33vw;
  width: 100vw;
  position: relative;
  background-size: cover;
  background-position: center;

  @include isMobile {
