.banner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  /* Create radial gradient */
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 60%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.banner-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: #fff;
  width: 100%;
  padding: 16px;
  text-align: center;
}

.banner-title h1 {
  font-size: 2.5em;

  @include isDesktop {
    font-size: 4em;
  }
}

.banner-subtitle {
  padding-top: 32px;
  font-size: 24px;
}

.content-block {
  display: flex;
  flex-direction: column;

  @include isDesktop {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  @include isMobile {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;

  @include isMobile {
    flex-wrap: wrap;
  }
}

.content-block-title {
  padding-bottom: 32px;
  text-align: center;
}

.content-block-title h2 {
  font-size: 2em;
  font-weight: bold;
  margin: 0;
}

.content-text {  
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  
  @include isDesktop {
    max-width: 70%;
    flex-basis: 70%;
  }
}

.content-button {
  @include isMobile {
    align-self: center;
  }
}

.button {
  margin-top: 32px;
  align-self: flex-start;

  display: inline-flex;
  align-items: center;
  cursor: pointer;

  padding: 0 16px;
  border-radius: 46px;
  height: 46px;
  
  background-color: #FCDC94;
  font-size: 16px;
  font-weight: 700;
  color: #92521b;
  box-shadow: 0 2px 5px 0 rgb(213 217 217 / 50%);
}

.button:hover {
  background-color: #f6d384;
  text-decoration: none;
}

.content-image-container {
  flex-grow: 1;
  overflow: hidden;  
  @include isDesktop {
    flex-basis: 30%;
  }
  @include isMobile {
    padding: 0 32px;
  }
}

.image-block .content-image-container {
  @include isDesktop {
    max-width: 40%;
  }
  @include isMobile {
    
  }
}

.google-maps {
  position: relative;
  padding-bottom: 75%; // This is the aspect ratio
  height: 0;
  overflow: hidden;
}
.google-maps iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}

.content-image-container.pad-left {
  @include isDesktop {
    padding-left: 32px;
  }
  @include isMobile {
    padding-top: 32px;
  }
}
    
.content-image-container.pad-right {
  @include isDesktop {
    padding-right: 32px;
  }
  @include isMobile {
    padding-bottom: 32px;
  }
}

.content-image {
  flex-grow: 1;
  border-radius: 20px;
  max-width: 100%;
  box-shadow: 0 2px 5px 0 rgb(213 217 217 / 50%);
}

.footer {
  display: flex;
  justify-content: space-around;
  background-color: $bg-footer;
  @include isMobile {
    flex-direction: column;
  }
  @include isTablet {
    flex-direction: row;
  }
  padding-top: 16px;
  padding-bottom: 16px;
}

.footer-logo {
  height: 40px;
}

.footer-item {
  display: flex;
  flex-direction: column;
  padding: 0 8px;
}

.footer-item.logo {
  align-self: center;
}

.footer-item-title {
  font-size: 18px;
  font-weight: 700;

  padding-top: 16px;
  padding-bottom: 16px;
  
  @include isDesktop {
    text-align: center;
  }
}

.footer-subsection {
  padding-bottom: 8px;
}

.footer-subsection-title {
  font-weight: 600;
}

.contact-section {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  
  @include isDesktop {
    padding-top: 64px;
    padding-bottom: 64px;
  }
  @include isMobile {
    padding-top: 32px;
    padding-bottom: 32px;
  }

}

.contact-text {
  // width: 450px;
  padding: 0 32px 48px 32px;
}

.contact-text-section {
  padding-bottom: 8px;
}

.contact-text-section-content {
  padding-left: 16px;
}

.map-container {
  display: flex;
  justify-content: center;
  padding: 0 32px;
  height: 450px;
  width: 600px;
}